
import { Options, Vue } from "vue-class-component";
@Options({
  name: "Timelength",
  props: ["value", "type"],
  emits: ["change"],
})
export default class Timelength extends Vue {
  value!: string;
  type!: string;
  emitChange() {
    const putValue = prompt("输入自定义值", this.value);
    // @ts-ignore
    if (isNaN(putValue)) return; //非法值忽略
    this.$emit("change", putValue, this.type);
  }
}
