
import { Options, Vue } from "vue-class-component";
@Options({
  name: "Clock",
  emits: ["click", "reset"],
  props: {
    clockType: {
      type: String,
    },
    clockTime: {
      type: String,
    },
    ratio: {
      type: Number,
    },
  },
  watch: {
    clockType(this: Clock) {
      this.setColor();
    },
    ratio(this: Clock) {
      this.setHeight();
    },
  },
})
export default class Clock extends Vue {
  clockType!: string;
  clockTime!: string;
  ratio!: number;
  startColor = "#FFA500";
  willEndColor = "#008000";
  a!: any;
  mounted() {
    this.setColor();
  }
  created() {}
  setColor() {
    this.clockType === "Session"
      ? ((this.$refs
          .bakcground as HTMLDivElement).style.background = this.startColor)
      : ((this.$refs
          .bakcground as HTMLDivElement).style.background = this.willEndColor);
  }
  setHeight() {
    (this.$refs.bakcground as HTMLDivElement).style.height =
      this.ratio * 100 + "%";
  }
}
